.preloader{
   position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #fff;
    z-index: 100;
}
.preloaderContainer{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}
.preloader img{
    width: 150px;
    margin: 0 auto;
}